<template>
  <section>
    <div class="container">
      <div class="debt-regist">
        <p class="title"><em>   </em><br> </p>
        <div class="list">
          <ul class="flex">
            <li class="key"></li>
            <li class="value">
              <input type="text" placeholder=" " v-model="debtVO.creditor">
              <button @click="getAddressList" class="human"></button>
            </li>
          </ul>
          <ul class="flex">
            <li class="key"></li>
            <li class="value">
              <input type="text" placeholder=" " v-model="debtVO.amt_contract">
              <em></em>
            </li>
          </ul>
          <ul class="flex">
            <li class="key"></li>
            <li class="value">
              <input v-if="inter_type == 'percent'" type="text" placeholder=" " v-model="debtVO.interest">
              <input v-if="inter_type == 'won'" type="text" placeholder=" " v-model="debtVO.all_amt_repay_i">
              <button @click="changeInterType('percent')" class="onoff per" :class="{'on':inter_type == 'percent'}">%</button>
              <button @click="changeInterType('won')" class="onoff won" :class="{'on':inter_type == 'won'}"></button>
            </li>
          </ul>
          <ul class="flex">
            <li class="key"></li>
            <li class="value">
              <!-- <input type="text" placeholder=" " v-model="debtVO.ymd_loan"> -->
              <datepicker v-model="debtVO.ymd_loan" :language="ko" :format="Common.formatDateDot" class="div-date" :placeholder="' '"></datepicker>
              <button class="cal"></button>
            </li>
          </ul>
          <ul class="flex">
            <li class="key"></li>
            <li class="value">
              <!-- <input type="text" placeholder=" " v-model="debtVO.ymd_loanend"> -->
              <datepicker v-model="debtVO.ymd_loanend" :language="ko" :format="Common.formatDateDot" class="div-date" :placeholder="' '"></datepicker>
              <button class="cal"></button>
            </li>
          </ul>
        </div>

        <div class="debt-acco">
          <ul>
            <li>
              <div class="top">
                <a @click="toggleAcco"> </a>
              </div>
              <div class="body">
                <div class="flex">
                  <p></p>
                  <p>
                    <select @change="chgRepMethod" v-model="debtVO.rep_method">
                      <option value="00" disabled="disabled"></option>
                      <option value="03"></option>
                      <option value="01"></option>
                      <option value="02"></option>
                    </select>
                  </p>
                </div>
                <div class="flex">
                  <p></p>
                  <p>
                    <select v-model="debtVO.inter_pay_cycle">
                      <option value="00" disabled="disabled"></option>
                      <option value="01"></option>
                      <option value="02"></option>
                      <option value="03"></option>
                      <option value="04"></option>
                      <option value="05"></option>
                    </select>
                  </p>
                </div>
                <div class="flex">
                  <p></p>
                  <p>
                    <select v-model="debtVO.inter_pay_day">
                      <option value="00" disabled="disabled"></option>
                      <option :value="(n+'').length==1?'0'+n:n" v-for="n in 31" :key="n" v-text="n + ''"></option>
                    </select>
                  </p>
                </div>
              </div>
            </li>
          </ul>
        </div>

      </div>
      <div class="btn-wrap float">
        <a @click="saveDebt" class="solid blue box"></a>
      </div>
    </div>

  </section>
</template>

<script>
import Constant from "@/assets/js/constant.js";
import Common from "@/assets/js/common.js";
import datepicker from "vuejs-datepicker";
import { ko } from "vuejs-datepicker/dist/locale";

export default {
  name: "DebtRegister",
  data() {
    return {
      ko: ko,
      debtVO: {
        rep_method: "00",
        inter_pay_cycle: "00",
        inter_pay_day: "00"
      },
      inter_type: "percent",
      Common: Common
    };
  },
  components: {
    datepicker
  },
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {
    window.resultAddress = this.resultAddress;
    this.ko.ymd = true;
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    saveDebt: function() {
      var _this = this;

      this.debtVO.ymd_loan = Common.formatDate(this.debtVO.ymd_loan);
      this.debtVO.ymd_loanend = Common.formatDate(this.debtVO.ymd_loanend);
      this.debtVO.inter_type = this.inter_type;

      this.$router.push({
        path: "/debt/regDetail",
        query: { debtVO: _this.debtVO }
      });
    },
    changeInterType: function(type) {
      if (type == this.inter_type) {
        return;
      }
      this.inter_type = type;
    },
    getAddressList: function() {
      if (Constant.userAgent == "iOS") {
        Jockey.on("getAddressList", function(param) {
          resultAddress(param.src_nm_person, param.src_hp);
        });
      } else if (Constant.userAgent == "Android") {
        window.Android.getAddressList();
      }
    },
    resultAddress: function(nm_person, hp) {
      this.debtVO.name = nm_person;
    },
    toggleAcco: function(param) {
      var btn = $(param.target);
      if (btn.hasClass("on")) {
        btn
          .closest("li")
          .find(".body")
          .slideUp(500, "easeInOutExpo");
        btn.removeClass("on");
      } else {
        btn
          .closest("li")
          .find(".body")
          .slideDown(500, "easeInOutExpo");
        btn.addClass("on");
      }
      param.preventDefault();
    },
    chgRepMethod: function() {
      var key = this.debtVO.rep_method;
      if (key == "03") {
        this.debtVO.inter_pay_cycle = "04";
      }
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
.vdp-datepicker__calendar {
  position: fixed;
  font-size: 13px;
  line-height: 40px;
}
.vdp-datepicker__calendar header {
  position: static;
}
.div-date {
  text-align: right;
}
</style>



