<template>
  <section>
    <div class="loading success" v-if="result=='success'">
      <em>  .</em><br>   <br>  .
    </div>
    <div class="loading fail" v-else>
      <em> .</em><br>  <br />   .
    </div>
    <div class="btn-wrap float">
      <a @click="clickGohome()" class="solid box blue"></a>
    </div>
    <!-- <div class="btn-wrap col2">
      <a class="btn-solid"></a>
      <a class="btn-stroke"></a>
    </div> -->
  </section>
</template>

<script>
export default {
  name: "CreditRaiseResult",
  data() {
    return {
      result: ""
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {
    this.result = this.$route.params.result;
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    clickGohome: function() {
      this.$router.push("/credit/raiseMain");
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



