<template>
  <section>
    <div class="tab">
    <div class="wrap">
              <a @click="clickTab('def')" :class="{on: this.listType=='def'}"></a>
              <a @click="clickTab('etc')" :class="{on: this.listType=='etc'}"></a>
          </div>
      </div>
      
      <template v-if="this.listType=='def'">
      <div class="container">
          <div v-if="overdueList.length == 0" class="nodata">  </div>
          <div v-else class="col-top">
              <div>
                  <p class="key"><em>()</em></p>
                  <p class="value">{{formatNumber(overdueSumAmt)}}</p>
              </div>
              <div>
                  <p class="key"><em>()</em></p>
                  <p class="value">{{formatNumber(steadSumAmt)}}</p>
              </div>
          </div>
      </div>

      <div v-if="overdueList.length != 0" class="box-list list01">
          <div v-for="overdueInfo in overdueList" :key="overdueInfo.index" class="item">
              <div class="top">
                  <p class="symbol"><img :src="overdueInfo.fcImg" alt=""/>{{overdueInfo.nm_fc}}</p>
                  <p v-if="overdueInfo.cd_type == '01'" class="text red"></p>
                  <p v-else class="text blue"></p>
              </div>
              <div class="number-wrap">
                  <div class="left">
                      <p class="key"></p>
                      <p class="number">{{formatNumber(overdueInfo.amt_delay)}}<em></em></p>
                  </div>
                  <div class="right">
                      <p class="key"></p>
                      <p class="value">{{formatNumber(Math.round(overdueInfo.amt_repay/10000))}} <em></em></p>
                  </div>
              </div>
              <div class="text-wrap">
                  <div class="left">
                      <p class="key"></p>
                      <p class="value">{{formatNumber(Math.round(overdueInfo.amt_frt_delay/10000))}} <em></em></p>
                  </div>
                  <div class="right">
                      <p class="key"></p>
                      <p class="value">{{formatDateDot(overdueInfo.ymd_delay)}}</p>
                  </div>
              </div>
          </div>

          <!--noti-->
          <div class="info-massage">      ()           .</div>
		  <div class="info-massage">/    10  5    .</div>
		  <div class="info-massage">    1  .</div>
		  <div class="info-massage">       ,             .</div>
      </div>
      </template>

      <template v-else>
      <div class="container">
          <div v-if="etcOverdueList.length == 0" class="nodata">  </div>
          <div v-else class="col-top">
              <div>
                  <p class="key"><em>()</em></p>
                  <p class="value">{{formatNumber(overdueEtcSumAmt)}}</p>
              </div>
          </div>
      </div>

      <div v-if="etcOverdueList != 0" class="box-list list01">
          <div v-for="etcOverdueInfo in etcOverdueList" :key="etcOverdueInfo.index" class="item">
              <div class="top">
                  <p class="symbol"><img :src="etcOverdueInfo.fcImg" alt=""/>{{etcOverdueInfo.nm_fc}}</p>
                  <p v-if="etcOverdueInfo.cd_type == '01'" class="text blue"></p>
                  <p v-else-if="etcOverdueInfo.cd_type == '02'" class="text blue"></p>
                  <p v-else-if="etcOverInfo.cd_type == '03'" class="text blue"></p>
              </div>
              <template v-if="etcOverdueInfo.cd_type == '01'"> <!--  -->
              	<div class="number-wrap">
	                    <div class="left">
	                    <p class="key"></p>
	                    <p class="number">{{formatNumber(etcOverdueInfo.amt_orderdue)}}<em></em></p>
	                </div>
	                <div class="right">
	                    <p class="key"></p>
	                    <p class="value">{{formatNumber(Math.round(etcOverdueInfo.amt_repay/10000))}} <em></em></p>
	                </div>
	            </div>
	            <div class="hide-con show">
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatDateDot(etcOverdueInfo.ymd_delay)}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatNumber(Math.round(etcOverdueInfo.amt_delay/10000))}} <em></em></p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatDateDot(etcOverdueInfo.ymd_repay)}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{etcOverdueInfo.cd_default}}</p>
                    </div>
                </div>
              </template>
              <template v-else-if="etcOverdueInfo.cd_type == '02'"> <!--  -->
              	<div class="hide-con show">
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{etcOverdueInfo.nm_agency}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatDateDot(etcOverdueInfo.ymd_delay)}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatNumber(Math.round(etcOverdueInfo.amt_delay/10000))}} <em></em></p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{etcOverdueInfo.no_case}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{etcOverdueInfo.cd_default}}</p>
                    </div>
                </div>
              </template>
              <template v-else-if="etcOverdueInfo.cd_type == '03'"> <!--  -->
              	<div class="number-wrap">
	                    <div class="left">
	                    <p class="key"></p>
	                    <p class="number">{{formatNumber(etcOverdueInfo.amt_delay)}}<em></em></p>
	                </div>
	                <div class="right">
	                    <p class="key"></p>
	                    <p class="value">{{formatNumber(Math.round(etcOverdueInfo.amt_repay/10000))}} <em></em></p>
	                </div>
	            </div>
	            <div class="hide-con show">
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{etcOverdueInfo.nm_agency}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatDateDot(etcOverdueInfo.ymd_default)}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatNumber(etcOverdueInfo.amt_delay)}} <em></em></p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{formatDateDot(etcOverdueInfo.ymd_repay)}}</p>
                    </div>
                    <div class="list">
                        <p class="left"></p>
                        <p class="right">{{etcoverdueInfo.cd_default}}</p>
                    </div>
                </div>
              </template>
          </div>
          
          <!--noti-->
          <div class="info-massage">()        .</div>
		  <div class="info-massage">()          5  .</div>
		  <div class="info-massage"> , ,  500         ,     .</div>
	 	  <div class="info-massage">        3 .</div>
		  <div class="info-massage">                      .</div>
	 	  <div class="info-massage">        5  .</div>
      </div>
      </template>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "creditOverdueInfo",
  data() {
    return {
      errMsg: "",
      listType: "def",
      //
      overdueSumAmt: "", //
      steadSumAmt: "", //
      overdueList: [], //list
      //()
      overdueEtcSumAmt: "", //()
      etcOverdueList: [] //() list
    };
  },
  component: {},
  computed: {},
  beforeCreate() {},
  created() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
    this.getOverdueInfo();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    // 
    clickTab: function(listType) {
      var _this = this;
      _this.listType = listType;
    },
    getOverdueInfo: function() {
      var _this = this;
      this.$http
        .get("/m/credit/getCreditOverdueInfo.json", {
          params: {}
        })
        .then(response => {
          //
          _this.overdueSumAmt = response.data.overdueSumAmt;
          _this.steadSumAmt = response.data.steadSumAmt;

          //ICON 
          var defList = response.data.overdueList;
          for (var i = 0; i < defList.length; i++) {
            defList[i].fcImg = "/m/fincorp/getFinCorpIcon.crz?cd_fc=" + defList[i].cd_fc;
          }
          _this.overdueList = defList;

          //() 
          _this.overdueEtcSumAmt = response.data.overdueEtcSumAmt;

          //ICON 
          var etcList = response.data.etcOverdueList;
          for (var i = 0; i < etcList.length; i++) {
            etcList[i].fcImg = "/m/fincorp/getFinCorpIcon.crz?cd_fc=" + etcList[i].cd_fc;
          }
          _this.etcOverdueList = etcList;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    formatDateDot: function(data) {
      return Common.formatDateDot(data);
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



