<template>
  <section>
    <div class="container">
        <div v-if="debtList.length == 0" class="nodata">  </div>
        <div v-else class="bar-top">
              <p class="key"></p>
              <div class="wrap">
                  <div class="left">{{formatNumber(ds_sum_amt_remain)}}<em></em></div>
                  <div class="right">
                      <p class="key"></p>
                      <p class="value">{{formatNumber(Math.round(ds_sum_amt_contract/10000))}} <em></em></p>
                  </div>
              </div>
              <div class="bar"><p :style="{width: ds_tot_repay_pc+'%'}"></p></div>
              <div class="bar-text-wrap">
                  <p class="bar-text"> <em>{{ds_tot_repay_pc}}%</em></p>
                  <p class="bar-card"><em>{{ds_debt_cnt}}</em></p>
              </div>
          </div>
      </div>
      
      <div v-if="debtList.length != 0" class="box-list list01">
          <div v-for="debtInfo in debtList" :key="debtInfo.index" class="item" @click="viewDetailDebt(debtInfo.no_manage_info)">
              <div class="top">
                  <p class="symbol"><img :src="debtInfo.fcImg" alt=""/>{{debtInfo.nm_fc}}</p>
                  <p class="text blue">{{debtInfo.cd_debt == '01'? '' : ''}}</p>
              </div>
              <div class="number-wrap">
                  <div class="left">
                      <p class="key"></p>
                      <p class="number">{{formatNumber(debtInfo.amt_remain)}}<em></em></p>
                  </div>
              </div>
              <div class="bar"><p :style="{width: debtInfo.repayPc+'%'}"></p></div>
              <div class="text-wrap">
                  <div class="left">
                      <p class="key"></p>
                      <p class="value">{{formatNumber(Math.round(debtInfo.amt_contract/10000))}} <em></em></p>
                  </div>
                  <div class="right">
                      <p class="key"></p>
                      <p class="value">{{formatDateDot(debtInfo.ymd_loan)}}</p>
                  </div>
              </div>
          </div>
          
          <div class="btn-wrap">
              <a @click="$router.push('/debt/main')" class="solid blue"></a>
          </div>

          <!-- noti -->
          <div class="info-massage">    .</div>
					<div class="info-massage">                   .</div>
					<div class="info-massage">             .</div>
					<div class="info-massage">                   .</div>
					<div class="info-massage">             .</div>
      </div>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "creditLoanInfo",
  data() {
    return {
      errMsg: "",
      //
      debtSum: "",
      ds_sum_amt_remain: "0", //
      ds_sum_amt_contract: "0", //
      ds_debt_cnt: "0", //
      ds_tot_repay_pc: "0", //  
      debtList: [] //list
    };
  },
  component: {},
  computed: {},
  beforeCreate() {},
  created() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
    this.getLoanInfo();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    getLoanInfo: function() {
      var _this = this;
      this.$http
        .get("/m/credit/getCreditLoanInfo.json", {
          params: {}
        })
        .then(response => {
          var debtSumInfo = response.data.debtSum;
          if (debtSumInfo != null) {
            _this.ds_sum_amt_remain = debtSumInfo.sum_amt_remain;
            _this.ds_sum_amt_contract = debtSumInfo.sum_amt_contract;
            _this.ds_debt_cnt = debtSumInfo.debt_cnt;
            //  
            if (debtSumInfo.sum_amt_contract > 0) {
              _this.ds_tot_repay_pc = Math.round(
                ((debtSumInfo.sum_amt_contract - debtSumInfo.sum_amt_remain) /
                  debtSumInfo.sum_amt_contract) * 100
              );
            }
          }
          _this.debtSum = debtSumInfo;

          var list = response.data.debtList;
          for (var i = 0; i < list.length; i++) {
            //ICON 
            list[i].fcImg =
              "/m/fincorp/getFinCorpIcon.crz?cd_fc=" + list[i].cd_fc;

            //  
            if (list[i].amt_contract > 0) {
              list[i].repayPc = Math.round(
                ((list[i].amt_contract - list[i].amt_remain) /
                  list[i].amt_contract) *
                  100
              );
            } else {
              list[i].repayPc = 0;
            }
          }

          _this.debtList = list;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    //  
    viewDetailDebt: function(no_manage_info) {
      this.$router.push({
        name: "debtDetail",
        params: { no_manage_info: no_manage_info }
      });
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    formatDateDot: function(data) {
      return Common.formatDateDot(data);
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



