<template>
  <section>
    <div class="tab">
           <div class="wrap">
               <a @click="clickTab('credit')" :class="{on: this.listType=='credit'}"></a>
               <a @click="clickTab('check')" :class="{on: this.listType=='check'}"></a>
           </div>
       </div>
       
       <!--  -->
       <template v-if="this.listType=='credit'">
       <div class="container">
           <div v-if="creditList.length == 0" class="nodata">  </div>
           <div v-else class="bar-top">
               <p class="key">{{useMonth}} </p>
               <div class="wrap">
                   <div class="left">{{formatNumber(creditAmtTotal)}}<em></em></div>
                   <div class="right">
                       <p class="key"></p>
                       <p class="value">{{formatNumber(Math.round(creditLimitTotal/10000))}} <em></em></p>
                  </div>
               </div>
               <div class="bar"><p :style="{width: creditTotLimitPc+'%'}"></p></div>
               <div class="bar-text-wrap">
                    <p class="bar-text">7  <em>{{creditTotLimitPc}}%</em></p>
                    <p class="bar-card"><em>{{creditCnt}}</em></p>
               </div>
           </div>
       </div>
       
       <div v-if="creditList.length != 0" class="box-list list01">
           <div v-for="creditInfo in creditList" :key="creditInfo.index" class="item expand">
               <div class="top">
                   <p class="symbol"><img :src="creditInfo.fcImg" alt=""/>{{creditInfo.nm_fc}}</p>
               </div>
               <div class="number-wrap">
                   <div class="left">
                       <p class="key"></p>
                       <p class="number">{{formatNumber(creditInfo.amt_total)}}<em></em></p>
                   </div>
               </div>
               <div class="bar"><p :style="{width: creditInfo.limitPc+'%'}"></p></div>
               <div class="flex-wrap col3">
                   <div class="left">
                       <p class="key"></p>
                       <p class="value">{{formatDateDot(creditInfo.ymd_open)}}</p>
                   </div>
                   <div class="center">
                       <p class="key"></p>
                       <p class="value">{{formatNumber(Math.round(creditInfo.amt_limit/10000))}} <em></em></p>
                   </div>
                   <div class="right">
                       <p class="key"></p>
                       <p class="value">{{formatNumber(Math.round(creditInfo.amt_ca_limit/10000))}} <em></em></p>
                   </div>
               </div>
               <div class="hide-con">
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(creditInfo.amt_lump_sum/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(creditInfo.amt_installment/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left">()</p>
                       <p class="right">{{formatNumber(Math.round(creditInfo.amt_short_card_loan/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(creditInfo.amt_check/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(creditInfo.amt_delay/10000))}} <em></em></p>
                   </div>
               </div>
               <button class="btn-expand"></button>
           </div>

           <!-- noti -->
           	<div class="info-massage">       .                 .</div>
			<div class="info-massage">  ()   .</div>
			<div class="info-massage">                   .</div>
			<div class="info-massage">  ()  .</div>
			<div class="info-massage">  (),    .</div>
			<div class="info-massage">                   .</div>
			<div class="info-massage">  1   .</div>
			<div class="info-massage">        .</div>
			<div class="info-massage">          .</div>
      </div>
      </template>

      <!--  -->
      <template v-else>
       <div class="container">
           <div v-if="checkList.length == 0" class="nodata">  </div>
           <div v-else class="bar-top">
               <p class="key">{{useMonth}} </p>
               <div class="wrap">
                   <div class="left">{{formatNumber(checkAmtTotal)}}<em></em></div>
                   <div class="right">
                       <p class="key"></p>
                       <p class="value">{{formatNumber(Math.round(checkLimitTotal/10000))}} <em></em></p>
                  </div>
               </div>
               <div class="bar"><p :style="{width: checkTotUsePc+'%'}"></p></div>
               <div class="bar-text-wrap">
                    <p class="bar-text">7  <em>{{checkTotUsePc}}%</em></p>
                    <p class="bar-card"><em>{{checkCnt}}</em></p>
               </div>
           </div>
       </div>

       <div v-if="checkList.length != 0" class="box-list list01">
           <div v-for="checkInfo in checkList" :key="checkInfo.index" class="item expand">
               <div class="top">
                   <p class="symbol"><img :src="checkInfo.fcImg" alt=""/>{{checkInfo.nm_fc}}</p>
               </div>
               <div class="number-wrap">
                   <div class="left">
                       <p class="key"></p>
                       <p class="number">{{formatNumber(checkInfo.amt_total)}}<em></em></p>
                   </div>
               </div>
               <div class="bar"><p :style="{width: checkInfo.usePc+'%'}"></p></div>
               <div class="flex-wrap col3">
                   <div class="left">
                       <p class="key"></p>
                       <p class="value">{{formatDateDot(checkInfo.ymd_open)}}</p>
                   </div>
                   <div class="center">
                       <p class="key"></p>
                       <p class="value">{{formatNumber(Math.round(checkInfo.amt_limit/10000))}} <em></em></p>
                   </div>
                   <div class="right">
                       <p class="key"></p>
                       <p class="value">{{formatNumber(Math.round(checkInfo.amt_ca_limit/10000))}} <em></em></p>
                   </div>
               </div>
               <div class="hide-con">
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(checkInfo.amt_lump_sum/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(checkInfo.amt_installment/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left">()</p>
                       <p class="right">{{formatNumber(Math.round(checkInfo.amt_short_card_loan/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(checkInfo.amt_check/10000))}} <em></em></p>
                   </div>
                   <div class="list">
                       <p class="left"></p>
                       <p class="right">{{formatNumber(Math.round(checkInfo.amt_delay/10000))}} <em></em></p>
                   </div>
               </div>
               <button class="btn-expand"></button>
           </div>

           <!-- noti -->
           	<div class="info-massage">       .                 .</div>
			<div class="info-massage">  ()   .</div>
			<div class="info-massage">                   .</div>
			<div class="info-massage">  ()  .</div>
			<div class="info-massage">  (),    .</div>
			<div class="info-massage">                   .</div>
			<div class="info-massage">  1   .</div>
			<div class="info-massage">  15 .</div>
			<div class="info-massage">        .</div>
			<div class="info-massage">  ( )    .</div>
       </div>
      </template>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "creditCardInfo",
  data() {
    return {
      errMsg: "",
      listType: "credit",
      useMonth: "",
      //
      creditCnt: "", //
      creditList: [], //list
      creditAmtTotal: "", //
      creditLimitTotal: "", //
      creditTotLimitPc: "", // 
      //
      checkCnt: "", //
      checkList: [], //list
      checkAmtTotal: "", //
      checkLimitTotal: "", //
      checkTotUsePc: "" //
    };
  },
  component: {},
  computed: {},
  beforeCreate() {},
  created() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
    this.setUseMonth();
    this.getCardInfo();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    // 
    clickTab: function(listType) {
      var _this = this;
      _this.listType = listType;
    },
    setUseMonth: function() {
      var _this = this;
      var date = new Date();

      if (date.getDate() < 15) {
        date.setMonth(date.getMonth() - 1);
      }

      if (date.getMonth() == 0) {
        _this.useMonth = "12";
      } else {
        _this.useMonth = date.getMonth();
      }
    },
    getCardInfo: function() {
      var _this = this;
      this.$http
        .get("/m/credit/getCreditCardInfo.json", {
          params: {}
        })
        .then(response => {
          //
          _this.creditCnt = response.data.creditCnt;

          var creditAmtTotal = response.data.creditAmtTotal;
          var creditLimitTotal = response.data.creditLimitTotal;
          _this.creditAmtTotal = creditAmtTotal;
          _this.creditLimitTotal = creditLimitTotal;

          // 
          if (creditLimitTotal > 0) {
            _this.creditTotLimitPc = (creditAmtTotal / creditLimitTotal) * 100;
          } else {
            _this.creditTotLimitPc = 0;
          }

          var cdList = response.data.creditList;
          for (var i = 0; i < cdList.length; i++) {
            //ICON 
            cdList[i].fcImg = "/m/fincorp/getFinCorpIcon.crz?cd_fc=" + cdList[i].cd_fc;

            // 
            if (cdList[i].amt_limit > 0) {
              cdList[i].limitPc =
                Math.round((cdList[i].amt_total / cdList[i].amt_limit) * 100);
            } else {
              cdList[i].limitPc = 0;
            }
          }
          _this.creditList = cdList;

          //
          _this.checkCnt = response.data.checkCnt;

          var checkAmtTotal = response.data.checkAmtTotal;
          var checkLimitTotal = response.data.checkLimitTotal;
          _this.checkAmtTotal = checkAmtTotal;
          _this.checkLimitTotal = checkLimitTotal;

          // 
          if (checkAmtTotal + creditAmtTotal > 0) {
            _this.checkTotUsePc =
              Math.round((checkAmtTotal / (checkAmtTotal + creditAmtTotal)) * 100);
          } else {
            _this.checkTotUsePc = 0;
          }

          var ckList = response.data.checkList;
          for (var i = 0; i < ckList.length; i++) {
            //ICON 
            ckList[i].fcImg = "/m/fincorp/getFinCorpIcon.crz?cd_fc=" + ckList[i].cd_fc;

            //
            ckList[i].usePc = (ckList[i].amt_total / checkAmtTotal) * 100;
          }

          _this.checkList = ckList;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    formatDateDot: function(data) {
      return Common.formatDateDot(data);
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



