<template>
  <div>
    <button class="open-menu"></button>
    <div class="gnb-wrap">
      <div class="top">
        <div class="left">
          <button class="alarm" @click="clickButton('/etc/alarmHistory')"></button>
          <button class="logout" @click="chkLogout"></button>
        </div>
        <div class="right">
          <button class="gnb-close"></button>
        </div>
      </div>
      <div class="scroll-wrap">
        <div class="info">
          <p class="name">{{this.$store.state.user.nmPerson}}</p>
          <p class="date">  {{connectTime}} </p>
          <a @click="clickButton('/mypage/info')">MYPAGE</a>
        </div>
        <div class="links">
          <a @click="clickButton('/etc/faqMain')"></a>
          <a @click="clickButton('/mypage/cert')"></a>
          <a @click="clickButton('/etc/noticeMain')">/</a>
          <a href="#"></a>
          <a href="#"></a>
          <a @click="clickButton('/share/main')"></a>
          <a href="#"></a>
          <a @click="clickButton('/news/main')"></a>
        </div>
        <dl id="gnb">
          <dt></dt>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>

          <dt></dt>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>

          <dt></dt>
          <dd><a href="#">/ </a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"> </a></dd>

          <dt></dt>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
          <dd><a href="#"></a></dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
import Constant from "./../../assets/js/constant.js";

export default {
  name: "error",
  data() {
    return {
      errMsg: "",
      connectTime: ""
    };
  },
  component: {},
  // computed () {
  // },
  beforeCreate() {},
  created() {
    this.getConnectTime();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //logout 
    chkLogout: function() {
      let _this = this;
      _this.$dialogs
        .confirm(" ", Constant.options)
        .then(res => {
          if (res.ok) {
            _this.$router.push("/mypage/logout");
          } else {
            return false;
          }
        });
    },
    //  
    getConnectTime: function() {
      var _this = this;
      this.$http
        .get("/m/person/getPersonConnectTime.json", {
          params: {}
        })
        .then(response => {
          _this.connectTime = response.data.connectTime;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    clickButton: function(page) {
      // GNB Close Event
      $(".gnb-wrap").removeClass("on");
      $("body").removeClass("not-scroll");
      this.$router.push(page);
    }
    // goBack: function() {
    //   var historySize = history.length;
    //   if (historySize == undefined || historySize == 0) {
    //     if (Constant.userAgent == "Android") {
    //       window.Android.exitApp();
    //     }
    //   } else {
    //     history.back();
    //   }
    // }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



