<template>
  <div id="container">
    <svg :viewBox="viewBoxDimensions" preserveAspectRatio="xMidYMid meet">
      <!-- background circle -->
      <path
        id="backgroundBar"
        ref="backgroundBar"
        :d="backgroundBarPath"
        :stroke-width="strokeWidth"
        fill="none"
      />

      <!-- data circle -->
      <path
        id="dataBar"
        ref="dataBar"
        :d="dataBarPath"
        :stroke-width="strokeWidth"
        fill="none"
        >
        <!-- dataBar animation -->
        <animateTransform
          ref="dataBarAnimate"
          attributeName="transform"
          type="rotate"
          :from="rotateFrom"
          :to="rotateTo"
          dur="2s"
          begin="1s"
          fill="freeze"
          keySplines="0.1 0.8 0.2 1;"
          keyTimes="0; 1"
          calcMode="spline"
        />
      </path>

      <!-- hide bottom half of circle-->
      <rect id="clipRectangle"
        :x="rectOffsetX"
        :y="centerY"
        :width="rectWidth"
        :height="rectHeight"
      />

      <!-- beautify -->
      <circle id="leftEndPoint" :cx="leftX" :cy="centerY" :r="beautifyCircleRadius"/>
      <circle id="rightEndPoint" :cx="rightX" :cy="centerY" :r="beautifyCircleRadius"/>

      <!-- data point (beautify) -->
      <circle
        id="dataPoint"
        :cx="leftX"
        :cy="centerY"
        :r="beautifyCircleRadius"
        >
        <!-- dataBar point animation -->
        <animateTransform
          ref="dataPointAnimate"
          attributeName="transform"
          type="rotate"
          :from="rotateFrom"
          :to="rotateTo"
          dur="2s"
          begin="1s"
          fill="freeze"
          keySplines="0.1 0.8 0.2 1;"
          keyTimes="0; 1"
          calcMode="spline"
        />
      </circle>

      <text id="centerText" text-anchor="middle" :x="centerX" :y="centerY">
        {{ this.text }}
      </text>
    </svg>
  </div>
</template>

<script>
export default {
  name: "GaugeCircle",
  data: function() {
    return {
      // viewBox , svg   element     (width/height)  .
      //  svg    #container width/height  .
      viewBoxX: 0, // svg x-offset
      viewBoxY: 0, // svg y-offset
      viewBoxWidth: 100, // svg viewBox width.
      viewBoxHeight: 50, // svg viewBox height.
      radius: 50, //  .
      strokeWidth: 7 // .
    };
  },
  props: {
    value: {
      //  . 0~1.
      type: Number,
      default: 0.0
    },
    text: {
      //    .
      type: String,
      default: ""
    }
  },
  computed: {
    viewBoxDimensions() {
      let xOffset = this.viewBoxX - this.strokeWidth / 2;
      let yOffset = this.viewBoxY - this.strokeWidth / 2;
      let boxWidth = this.viewBoxWidth + this.strokeWidth;
      let boxHeight = this.viewBoxHeight + this.strokeWidth;
      //  viewBoxWidth/Height      .
      // =>      
      // =>   viewbox 
      // ==> xOffset =     
      // ==> yOffset =     
      // ==> boxWidth = width   ( .5,  .5)  
      // ==> boxheight = height   ( .5  .5)  
      return [xOffset, yOffset, boxWidth, boxHeight].join(" ");
    },
    backgroundBarPath() {
      let startingPoint = "M" + this.leftX + "," + this.centerY;
      let arc = "a1,1 0 0 1" + this.radius * 2 + ",0";
      // e.g. M0,0 a1,1 0 0 1 100,0'
      // M0,0 = 0,0   
      // a = arc   (  )
      // 1,1 =  1, 1 
      // 0 = ??
      // 0 = ??
      // 1 = x-axis  
      // 100,0  
      return startingPoint + " " + arc;
    },
    dataBarPath() {
      let startingPoint = "M" + this.leftX + "," + this.centerY;
      let arc = "a1,1 1 0 0" + this.radius * 2 + ",0";
      return startingPoint + " " + arc;
    },
    centerX() {
      return this.viewBoxWidth / 2;
    },
    centerY() {
      return this.viewBoxHeight;
    },
    leftX() {
      return this.centerX - this.radius;
    },
    rightX() {
      return this.centerX + this.radius;
    },
    beautifyCircleRadius() {
      return this.strokeWidth / 2;
    },
    rectOffsetX() {
      return (this.strokeWidth * -1) / 2;
    },
    rectWidth() {
      return this.viewBoxWidth + this.strokeWidth * 2;
    },
    rectHeight() {
      return this.radius + this.strokeWidth;
    },
    rotateAngle() {
      let angle = this.value * 180;
      return angle;
    },
    rotateFrom() {
      return [0, this.centerX, this.centerY].join(" ");
    },
    rotateTo() {
      console.log(this, "rotateto");
      let angle = 180 * this.value;
      return [angle, this.centerX, this.centerY].join(" ");
    }
  },
  methods: {
    test() {}
  }
};
</script>

<style scoped>
#container {
  width: 100%;
  margin: auto;
  padding: 0;
  --backgroundColor: #ffffff; /* svg   */
  --backgroundBarColor: #d2d3d7; /*    */
  --dataBarColor: #fc6e6d; /*  data  */
}

svg {
  width: 100%;
  height: 100%;
  background-color: var(--backgroundColor);
}

#backgroundBar {
  /* background circle */
  stroke: var(--backgroundBarColor);
}

#dataBar {
  /* data circle */
  stroke: var(--dataBarColor);
  clip-path: url(#clipRectangle);
}

#clipRectangle {
  /* dataBar  */
  fill: var(--backgroundColor);
}

#leftEndPoint {
  /* backgroundBar   , dataBar   . */
  fill: var(--dataBarColor);
}
#rightEndPoint {
  /* backgroundBar   , backgroundBar  . */
  fill: var(--backgroundBarColor);
}

#dataPoint {
  fill: var(--dataBarColor);
}

#centerText {
  font-size: 19px;
  text-align: center;
  position: absolute;
  bottom: 0;
  color: #222;
  width: 100%;
  line-height: 1;
}
</style>


