<template>
  <section v-if="seen">
    <div class="con-top share-top">
      <p v-if="cd_share=='01'">  <br>    .<br><br><br>
          <em>*       .</em>
      </p>
      <p v-else-if="cd_share=='02'"><em>finset   </em> <br>  <br>  , <br><em>  </em><br>  <em>  .</em></p>
    </div>

    <div class="tab">
      <div class="wrap">
        <a @click="clickTab('req')" :class="{on: type_list=='req'}">  {{cd_share=='01'? '' : ''}}</a>
        <a @click="clickTab('offer')" :class="{on: type_list=='offer'}">{{cd_share=='01'? '' : ''}}  </a>
      </div>
    </div>
    
    <template v-if="type_list=='req'">
    <div v-if="reqSbList.length == 0 && reqPmList.length == 0" class="nodata">  </div>
    <div v-else class="box-list noMG list02">
      <p class="header col"> </p>
      <div v-if="reqSbList.length == 0">  .</div>
      <div v-for="reqSbInfo in reqSbList" :key="reqSbInfo.index" class="item">
        <a @click="goSettingDetail('req', '01', reqSbInfo.seq_share, '')" class="share">
          <span class="name">{{reqSbInfo.offer_nm_person}}</span>
          <span class="state"></span>
          <span>{{reqSbInfo.offer_hp}}</span>
        </a>
      </div>
      <p class="header col"> {{cd_share=='01'? '' : ''}}</p>
      <div v-if="reqPmList.length == 0">  .</div>
      <div v-for="reqPmInfo in reqPmList" :key="reqPmInfo.index" class="item">
        <a @click="goSettingDetail('req', '02', reqPmInfo.seq_share, reqPmInfo.dt_stt_offer)" class="share">
          <p>
            <span class="name">{{reqPmInfo.offer_nm_person}}</span>
            <span>{{reqPmInfo.offer_hp}}</span>
          </p>
          <p class="mt10">
            <span><strong>{{formatDateDot(reqPmInfo.dt_stt_offer)}}</strong></span>
            <span class="state">
              <em v-if="reqPmInfo.yn_credit_info=='Y'"></em>
              <em v-if="reqPmInfo.cd_share=='01' && reqPmInfo.yn_debt_info=='Y'"></em>
              <em v-if="reqPmInfo.yn_asset_info=='Y'"></em>
              <em v-if="reqPmInfo.yn_consume_info=='Y'"></em>
              <em v-if="reqPmInfo.cd_share=='02' && reqPmInfo.yn_debt_info=='Y'"></em>
            </span>
          </p>
        </a>
      </div>
    </div>

    <div class="btn-wrap" style="margin-bottom:100px;"> 
      <a @click="goHistory('req')" class="stroke blue">  </a>
    </div>

    <div class="btn-wrap float">
      <a @click="goNewRequest();" class="solid blue box"> </a>
    </div>
    </template>

    <template v-else-if="type_list=='offer'">
    <div v-if="offerSbList.length == 0 && offerPmList.length == 0" class="nodata">  </div>
    <div v-else class="box-list noMG list02">
      <p class="header col"> </p>
      <div v-if="offerSbList.length == 0">  .</div>
      <div v-for="offerSbInfo in offerSbList" :key="offerSbInfo.index" class="item">
        <a @click="goSettingDetail('offer','01', offerSbInfo.seq_share, '')" class="share">
          <span class="name">{{offerSbInfo.req_nm_person}}</span>
          <span class="state"></span>
          <span>{{offerSbInfo.req_hp}}</span>
        </a>
      </div>
      <p class="header col"> {{cd_share=='01'? '' : ''}}</p>
      <div v-if="offerPmList.length == 0">  .</div>
      <div v-for="offerPmInfo in offerPmList" :key="offerPmInfo.index" c class="item">
        <a @click="goSettingDetail('offer','02', offerPmInfo.seq_share, offerPmInfo.dt_stt_offer)" class="share">
          <p>
            <span class="name">{{offerPmInfo.req_nm_person}}</span>
            <span>{{offerPmInfo.req_hp}}</span>
          </p>
          <p class="mt10">
            <span><strong>{{formatDateDot(offerPmInfo.dt_stt_offer)}}</strong></span>
            <span class="state">
              <em v-if="offerPmInfo.yn_credit_info=='Y'"></em>
              <em v-if="offerPmInfo.cd_share=='01' && offerPmInfo.yn_debt_info=='Y'"></em>
              <em v-if="offerPmInfo.yn_asset_info=='Y'"></em>
              <em v-if="offerPmInfo.yn_consume_info=='Y'"></em>
              <em v-if="offerPmInfo.cd_share=='02' && offerPmInfo.yn_debt_info=='Y'"></em>
            </span>
          </p>
        </a>
      </div>
    </div>

    <div class="btn-wrap">
      <a @click="goHistory('offer')" class="stroke blue">  </a>
    </div>
    </template>

  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";

import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "ShareMain",
  data() {
    return {
      errMsg: "",
      seen: false,
      cd_share: "01", //(01) / (02) 
      type_list: "req", //(req) / (offer) 
      currentDate: "", //
      reqSbList: [], //list()
      reqPmList: [], //list()
      offerSbList: [], //list()
      offerPmList: [] //list()
    };
  },
  components: {},
  computed: {},
  beforeCreate() {},
  created() {
    if ("" != this.$route.query.cd_share && this.$route.query.cd_share != null) {
      this.cd_share = this.$route.query.cd_share;
    }
    if ("" != this.$route.query.type_list && this.$route.query.type_list != null) {
      this.type_list = this.$route.query.type_list;
    }

    this.$store.state.header.type = "sub";
    if ("01" == this.cd_share) {
      this.$store.state.title = " ";
    } else if ("02" == this.cd_share) {
      this.$store.state.title = " ";
    }

    this.getMainInfo();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    // 
    clickTab: function(type_list) {
      var _this = this;
      _this.type_list = type_list;
    },
    //
    getMainInfo: function() {
      var _this = this;
      this.$http
        .get("/m/customercenter/listShareInfoMain.json", {
          params: { cd_share: _this.cd_share }
        })
        .then(response => {
          _this.currentDate = response.data.currentDate;
          _this.reqSbList = response.data.reqSbList;
          _this.reqPmList = response.data.reqPmList;
          _this.offerSbList = response.data.offerSbList;
          _this.offerPmList = response.data.offerPmList;
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });

        _this.seen = true;
    },
    formatDateDot: function(data) {
      return Common.formatDateDot(data);
    },
    ///  
    goSettingDetail: function(type_list, share_status, seq_share, dt_stt_offer) {

      var _this = this;

      if("01" == _this.cd_share){ //
        if ("req" == type_list) { // 
          if ("01" == share_status) { //
            this.$router.push({
              name: "shareReqSetting",
              query: { cd_share: _this.cd_share, seq_share: seq_share }
            });
          }else if("02" == share_status){ //
            if(dt_stt_offer == _this.currentDate){
              this.$router.push({
                name: "shareDetail",
                query: { seq_share: seq_share }
              });
            }
          }
        }else{ // 
          if ("01" == share_status) { //
            this.$router.push({
              name: "shareOfferSetting",
              query: { cd_share: _this.cd_share, seq_share: seq_share }
            });
          }else if("02" == share_status){ //
            if(dt_stt_offer == _this.currentDate){
              this.$router.push({
                name: "shareOfferSetting",
                query: { cd_share: _this.cd_share, seq_share: seq_share }
             });
            }
          }
        }
      }else{ //
        if ("req" == type_list) { // 
          this.$router.push({
            name: "shareReqSetting",
            query: { cd_share: _this.cd_share, seq_share: seq_share }
          });
        }else{ // 
          this.$router.push({
            name: "shareOfferSetting",
            query: { cd_share: _this.cd_share, seq_share: seq_share }
          });
        }
      }

    },
    // 
    goHistory: function(type_list) {
      var _this = this;
      this.$router.push({
        name: "shareHistory",
        query: { cd_share: _this.cd_share, type_list: type_list }
      });
    },
    // 
    goNewRequest: function() {
      var _this = this;
      this.$router.push({
        name: "shareNewRequest",
        query: { cd_share: _this.cd_share }
      });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



