      <p class="text">   ,     1     </p>
      <p class="spec"><span><em>2.7%</em></span><span><em>130%</em></span><span><em>6</em></span></p>
    </div>

    <div class="goods-detail mt50">
      <div class="container">
        <div>
          <p class="key"></p>
          <p class="value"></p>
        </div>
        <div>
          <p class="key"> </p>
          <p class="value">100%</p>
        </div>
      </div>
    </div>

  </section>
</template>

<script>
export default {
  name: "GoodsStockDetail",
  data() {
    return {};
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {}
};
</script>

