<template>
  <section>
    <div v-if="isInit" class="con-top cut-top">
      <p><em>     </em><br><br>  </p>
      <a @click="openInfo()">?</a>
    </div>

    <div v-if="!isInit && debtList.length==0" class="con-top cut-top">
      <p><em>    .</em></p>
    </div>
    <div v-if="!isInit && debtList.length!=0" class="cut-result-top">
      <div v-for="debtInfo in debtList" :key="debtInfo.index" class="item">
        <div class="top">
          <p class="symbol"><img :src="debtInfo.fcImg" alt="" />{{debtInfo.nm_fc}}</p>
          <p class="date">{{formatDateDot(debtInfo.ymd_loan)}}</p>
        </div>
        <div v-for="cutItem in debtInfo.cutItems" :key="cutItem.index">
          <p>{{getCodeName(cutItem)}}</p>
        </div>
        <div class="goods-benefit">
          <div>{{debtInfo.ever_interest}}<em> %</em></div>
          <div>{{formatNumber(debtInfo.amt_contract)}}<em> </em></div>
        </div>
        <a v-if="'' != debtInfo.tel && debtInfo.tel != null" @click="callFc(debtInfo.tel)" class="btn"> </a>
      </div>
    </div>

    <div class="accodion" style="margin-bottom:100px;">
      <div class="info-massage">      .</div>
      <ul>
        <li :class="{on: ynCredit}">
          <div class="top"><a @click="changeItems('credit')">   ?</a></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <input type="text" v-model="creditFixDate" readonly="readonly">
              </p>
            </div>
            <p class="text">  2              .</p>
          </div>
        </li>
        <li :class="{on: ynTurnover}">
          <div class="top"><a @click="changeItems('turnover')">    ?</a></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <input type="text" v-model="turnoverDate" readonly="readonly">
              </p>
            </div>
            <!--
                      <div class="flex">
                          <p></p>
                          <p><button class="search"></button></p>
                      </div>
                      <div class="flex">
                          <p></p>
                          <p><button class="search"></button></p>
                      </div>
                      -->
            <!-- <p class="text">      .</p> -->
          </div>
        </li>
        <li :class="{on: ynIncome}">
          <div class="top"><a @click="changeItems('income')">   ?</a></div>
          <div class="con">
            <!--
                      <div class="flex">
                          <p></p>
                          <p>
                              <select>
                                  <option>2018-08-01</option>
                              </select>
                          </p>
                      </div>
                      <div class="flex">
                          <p></p>
                          <p><input type="text" placeholder=""></p>
                      </div>
                      -->
            <div class="flex">
              <p></p>
              <!-- <p><input type="number" v-model="income" placeholder="" v-validate="'required'" data-vv-name=''></p>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p> -->
              <p><input type="number" v-model="income" placeholder="" autocomplete="off"></p>
            </div>
            <p class="text">      15%     .</p>
          </div>
        </li>
        <li :class="{on: ynDebt}">
          <div class="top"><a @click="changeItems('debt')">   ?</a></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <input type="text" v-model="debtFixDate" readonly="readonly">
              </p>
            </div>
            <div class="flex">
              <p></p>
              <!-- <p><input type="number" v-model="debtBfAmt" placeholder="" v-validate="'required'" data-vv-name=' '></p>
              <p class="warn" v-if="errors.has(' ')">{{errors.first(' ')}}</p> -->
              <p><input type="number" v-model="debtBfAmt" placeholder="" autocomplete="off"></p>
            </div>
            <div class="flex">
              <p></p>
              <!-- <p><input type="number" v-model="debtAtAmt" placeholder="" v-validate="'required'" data-vv-name=' '></p>
              <p class="warn" v-if="errors.has(' ')">{{errors.first(' ')}}</p> -->
              <p><input type="number" v-model="debtAtAmt" placeholder="" autocomplete="off"></p>
            </div>
            <p class="text">        (15%)  .</p>
          </div>
        </li>
        <li :class="{on: ynPos}">
          <div class="top"><a @click="changeItems('pos')">     ?</a></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <input type="text" v-model="posFixDate" readonly="readonly">
              </p>
            </div>
            <p class="text">      .</p>
          </div>
        </li>
        <li :class="{on: ynCert}">
          <div class="top"><a @click="changeItems('cert')">   ?</a></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <input type="text" v-model="certFixDate" readonly="readonly">
              </p>
            </div>
            <div class="flex">
              <p></p>
              <p>
                <select v-model="certification" v-validate="'required'" data-vv-name=''>
                  <option v-for="cert in certList" :key="cert.index" :value="cert.value">
                    {{ cert.text }}
                  </option>
                </select>
              </p>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
            </div>
            <p class="text">        .</p>
          </div>
        </li>
      </ul>
    </div>

    <!--
      <div class="btn-wrap">
          <a href="#" class="solid blue box"></a>
      </div>
      -->
    <div class="btn-wrap col2">
      <a class="stroke" @click="init()"></a>
      <a class="solid" @click="getReqIntrCut()"></a>
    </div>

    <vue-modal transitionName="zoom-in" name="info-modal" v-on:popclose="closeInfo()">
      <ReqIntrCutInfo slot="body" v-on:popclose="closeInfo()"></ReqIntrCutInfo>
    </vue-modal>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

import ReqIntrCutInfo from "./ReqIntrCutInfo.vue";

export default {
  name: "debtReqIntrCut",
  data() {
    return {
      isInit: true, // 
      currentDate: "", //
      ynCredit: false, // 
      ynTurnover: false, // 
      ynIncome: false, // 
      ynDebt: false, // 
      ynPos: false, // 
      ynCert: false, // 
      creditFixDate: "", // 
      turnoverDate: "", //
      income: "", //
      debtFixDate: "", // 
      debtBfAmt: "", //  
      debtAtAmt: "", //  
      posFixDate: "", // 
      certFixDate: "", // 
      certList: [
        { text: "", value: "" },
        { text: "", value: "01" },
        { text: "( )", value: "02" },
        { text: "", value: "03" },
        { text: "", value: "04" },
        { text: "", value: "05" },
        { text: "", value: "06" },
        { text: "", value: "07" },
        { text: "", value: "08" },
        { text: "", value: "09" },
        { text: "", value: "10" },
        { text: "", value: "11" },
        { text: "", value: "12" },
        { text: "", value: "13" },
        { text: "", value: "14" },
        { text: "", value: "15" }
      ], //list
      certification: "", //
      debtList: [] //list
    };
  },
  components: {
    ReqIntrCutInfo: ReqIntrCutInfo
  },
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {
    this.setCurrentDate();
    this.changeItems("init");
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    // 
    setCurrentDate: function() {
      var _this = this;
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1; //January is 0!
      var yyyy = today.getFullYear();

      if (dd < 10) {
        dd = "0" + dd;
      }

      if (mm < 10) {
        mm = "0" + mm;
      }

      _this.currentDate = yyyy + "-" + mm + "-" + dd;
    },
    //list 
    getReqIntrCut: function() {
      var _this = this;
      var cutItems = [];
      if (!_this.validate()) return false;

      if (_this.ynCredit) cutItems.push("01");
      if (_this.ynTurnover) cutItems.push("02");
      if (_this.ynIncome) cutItems.push("03");
      if (_this.ynDebt) cutItems.push("04");
      if (_this.ynPos) cutItems.push("05");
      if (_this.ynCert) cutItems.push("06");

      console.log("cutItems" + cutItems);
      console.log("creditFixDate" + _this.creditFixDate);
      console.log("turnoverDate" + _this.turnoverDate);
      console.log("income" + _this.income);
      console.log("debtFixDate" + _this.debtFixDate);
      console.log("posFixDate" + _this.posFixDate);
      console.log("certFixDate" + _this.certFixDate);

      var formData = new FormData();
      formData.append("cutItems", cutItems);
      formData.append("creditFixDate", _this.creditFixDate);
      formData.append("turnoverDate", _this.turnoverDate);
      formData.append("income", _this.income);
      formData.append("debtFixDate", _this.debtFixDate);
      formData.append("posFixDate", _this.posFixDate);
      formData.append("certFixDate", _this.certFixDate);

      this.$http
        .post("/m/debt/listReqIntrCut.json", formData)
        .then(function(response) {
          //ICON 
          var list = response.data.debtList;
          for (var i = 0; i < list.length; i++) {
            list[i].fcImg =
              "/m/fincorp/getFinCorpIcon.crz?cd_fc=" + list[i].cd_fc;
          }
          _this.debtList = list;

          _this.isInit = false;

          console.log(_this.isInit);
          console.log(_this.debtList);
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });
    },
    validate: function() {
      var _this = this;
      if (_this.ynIncome) {
        //
        if ("" == _this.income) {
          _this.$toast.center("  ");
          return false;
        }
      }
      if (_this.ynDebt) {
        //
        if ("" == _this.debtBfAmt) {
          _this.$toast.center("   ");
          return false;
        }
        if ("" == _this.debtAtAmt) {
          _this.$toast.center("   ");
          return false;
        }
        if (Number(_this.debtAtAmt) >= Number(_this.debtBfAmt) * 1.15) {
          _this.$toast.center("    ");
          return false;
        }
      }
      //
      if (_this.ynCert) {
        if ("" == _this.certification) {
          _this.$toast.center("  ");
          return false;
        }
      }

      return true;
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    formatDateDot: function(data) {
      return Common.formatDateDot(data);
    },
    getCodeName: function(data) {
      return Common.getCodeName("debt_cut_items", data);
    },
    openInfo: function() {
      var _this = this;
      _this.$modals.show("info-modal");
    },
    closeInfo: function() {
      var _this = this;
      _this.$modals.hide("info-modal");
    },
    // 
    callFc: function(tel) {
      console.log("tel" + tel);

      if (Constant.userAgent == "iOS") {
        Jockey.send("phoneCall", {
          phNum: tel
        });
      } else if (Constant.userAgent == "Android") {
        window.Android.phoneCall(tel);
      }
    },
    //
    changeItems: function(item) {
      var _this = this;
      var currentDate = _this.currentDate;

      if ("credit" == item) {
        _this.ynCredit = !_this.ynCredit;
      } else if ("turnover" == item) {
        _this.ynTurnover = !_this.ynTurnover;
      } else if ("income" == item) {
        _this.ynIncome = !_this.ynIncome;
      } else if ("debt" == item) {
        _this.ynDebt = !_this.ynDebt;
      } else if ("pos" == item) {
        _this.ynPos = !_this.ynPos;
      } else if ("cert" == item) {
        _this.ynCert = !_this.ynCert;
      } else if ("init" == item) {
        _this.ynCredit = false;
        _this.ynTurnover = false;
        _this.ynIncome = false;
        _this.ynDebt = false;
        _this.ynPos = false;
        _this.ynCert = false;
      }

      if (!_this.ynCredit) {
        this.creditFixDate = currentDate;
      }
      if (!_this.ynTurnover) {
        _this.turnoverDate = currentDate;
      }
      if (!_this.ynIncome) {
        _this.income = "";
      }
      if (!_this.ynDebt) {
        _this.debtFixDate = currentDate;
        _this.debtBfAmt = "";
        _this.debtAtAmt = "";
      }
      if (!_this.ynPos) {
        _this.posFixDate = currentDate;
      }
      if (!_this.ynCert) {
        _this.certFixDate = currentDate;
        _this.certification = "";
      }
    },
    //
    init: function() {
      var _this = this;

      _this.isInit = true;
      _this.changeItems("init");
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



