<template>
  <section>
    <div class="container mt30 pb90">
      <div class="checks">
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk1" v-model="chk1"><label for="chk1">       ,       . </label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk2" v-model="chk2"><label for="chk2">               .</label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk3" v-model="chk3"><label for="chk3">        .</label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk4" v-model="chk4"><label for="chk4">       5     .</label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk5" v-model="chk5"><label for="chk5">    .</label></p>
        </div>
        <input type="checkbox" id="check-all" @click="chkAll" v-model="chkall"><label for="check-all" class="mt30">  </label>
      </div>
    </div>

    <div class="btn-wrap float">
      <a @click="quitChkYn" class="solid box blue"></a>
    </div>
  </section>
</template>

<script>
import Constant from "./../../assets/js/constant.js";
export default {
  name: "MypageDrop",
  data() {
    return {
      chk1: false,
      chk2: false,
      chk3: false,
      chk4: false,
      chk5: false,
      chkall: false,
      checked: ""
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = " ";
  },
  created() {},
  beforeMount() {},
  mounted() {
    var _this = this;
    $(":checkbox").change(function() {
      if (_this.chk1 && _this.chk2 && _this.chk3 && _this.chk4 && _this.chk5) {
        _this.chkall = true;
        _this.checked = "check";
      } else {
        _this.chkall = false;
        _this.checked = "";
      }
    });
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    quitChkYn: function() {
      let _this = this;
      if (
        !_this.chk1 ||
        !_this.chk2 ||
        !_this.chk3 ||
        !_this.chk4 ||
        !_this.chk5
      ) {
        this.$toast.center("   ");
        setTimeout(function() {}, 2000);
        return false;
      }

      this.$dialogs
        .confirm("  ?", Constant.options)
        .then(res => {
          if (res.ok) {
            _this.dropPerson();
          } else {
            return false;
          }
        });
    },
    dropPerson: function() {
      let url = "/m/customercenter/customerQuitComp.json";
      let _this = this;

      var frm = new FormData();
      _this.$http
        .post(url, frm)
        .then(response => {
          if (response.data.result == "00") {
            //
            _this.$router.push("/mypage/dropDone");
          } else {
            _this.$toast.center(response.data.message);
          }
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });
    },
    chkAll: function() {
      var _this = this;
      if (_this.chkall) {
        _this.chkall = false;
      } else {
        _this.chkall = true;
      }

      if (_this.chkall) {
        // _this.checked = "check";
        _this.chk1 = true;
        _this.chk2 = true;
        _this.chk3 = true;
        _this.chk4 = true;
        _this.chk5 = true;
      } else {
        // _this.checked = "";
        _this.chk1 = false;
        _this.chk2 = false;
        _this.chk3 = false;
        _this.chk4 = false;
        _this.chk5 = false;
      }
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



