<template>
  <section>
    <dl class="setting-wrap">
      <dt></dt>
      <dd>
        <p class="key"></p>
        <p class="value checks round">
          <input type="radio" id="rds1" name="RadioGroup1"><label for="rds1"></label>
          <input type="radio" id="rds2" name="RadioGroup1"><label for="rds2"></label>
        </p>
      </dd>
      <dd class="bobtm">
        <p class="key"></p>
        <p class="value checks round">
          <input type="radio" id="rds3" name="RadioGroup2"><label for="rds3"></label>
          <input type="radio" id="rds4" name="RadioGroup2"><label for="rds4"></label>
        </p>
      </dd>
      <dd>
        <p class="key">   </p>
        <p class="value"><button class="btn-onoff"></button></p>
      </dd>
      <dd>
        <p class="key"><em class="red"></em></p>
        <p class="value"><button class="btn-onoff"></button></p>
      </dd>
      <dd class="bobtm">
        <p class="key"><em class="orange"></em></p>
        <p class="value"><button class="btn-onoff"></button></p>
      </dd>
      <dd>
        <p class="key">   </p>
        <p class="value"><button class="btn-onoff"></button></p>
      </dd>
    </dl>

    <div class="btn-wrap float">
      <a href="#" class="solid blue box"></a>
    </div>
  </section>
</template>

<script>
export default {
  name: "CommonCalSetting",
  data() {
    return {};
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {}
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



