<template>
<div id="wrapper">
	<!-- Header -->
	<header id="header">
		<div class="input-group">
			<div class="input-group-btn">
				<button type="button" class="ui-nav nav-back" onclick="history.back();"></button>
			</div>
			<h1> </h1>
		</div>
	</header>
	<!-- Content -->
	<section id="content">
		<div class="container">
			<div class="lead">
				<p>    .</p>
				<img id="img"/>
			</div>	
			<form name="frmUpdateDebtInfo" id="frmUpdateDebtInfo" method="post">
				<div class="form-inline">
					<input type="hidden" id ="no_manage_info" name="no_manage_info" :value="debtVO.no_manage_info"/>
					<div class="form-group">
						<label for="interest"> </label>
						<input type="number" class="form-control" id="int_interest" maxlength="6" autocomplete="off" v-model="debtVO.interest"/>
						<input type="hidden" name="interest" id="interest"/>
						<span class="form-control-feedback" aria-hidden="true">%</span>
					</div>
					<div class="form-group">
						<label for="repayment"></label>
						<select class="" data-header="" name="rep_method" id="repayment" v-model="debtVO.rep_method">
							<option value="3"></option>
							<option value="1"></option>
							<option value="2"></option>
						</select>
					</div>
					<div class="form-group">
						<label for="loan_mount"></label>
						<select class="" data-header="" name="loan_mount" id="loan_mount" v-model="debtVO.loan_mount">
							<option value=null></option>
							<option value="1">1</option>
							<option value="2">2</option>
							<option value="3">3</option>
							<option value="4">4</option>
							<option value="5">5</option>
							<option value="6">6</option>
							<option value="7">7</option>
							<option value="8">8</option>
							<option value="9">9</option>
							<option value="10">10</option>
						</select>
					</div>
					<div class="form-group">
						<label for="inter_pay_cycle"></label>
						<select class="" data-header="" name="inter_pay_cycle" id="inter_pay_cycle" v-model="debtVO.inter_pay_cycle">
							<option value="01"></option>
							<option value="02"></option>
							<option value="03"></option>
							<option value="04"></option>
							<option value="05"></option>
						</select>
					</div>
					<div class="form-group">
						<label for="inter_pay_day"></label>
						<select class="" data-header="" name="inter_pay_day" id="inter_pay_day" v-model="debtVO.inter_pay_day">
							<option value=null></option>
							<option value="01">1</option>
							<option value="02">2</option>
							<option value="03">3</option>
							<option value="04">4</option>
							<option value="05">5</option>
							<option value="06">6</option>
							<option value="07">7</option>
							<option value="08">8</option>
							<option value="09">9</option>
							<option value="10">10</option>
							<option value="11">11</option>
							<option value="12">12</option>
							<option value="13">13</option>
							<option value="14">14</option>
							<option value="15">15</option>
							<option value="16">16</option>
							<option value="17">17</option>
							<option value="18">18</option>
							<option value="19">19</option>
							<option value="20">20</option>
							<option value="21">21</option>
							<option value="22">22</option>
							<option value="23">23</option>
							<option value="24">24</option>
							<option value="25">25</option>
							<option value="26">26</option>
							<option value="27">27</option>
							<option value="28">28</option>
							<option value="29">29</option>
							<option value="30">30</option>
							<option value="31">31</option>
						</select>
					</div>
				</div>
			</form>
		</div>
		<div class="btn-fixed-bottom affix-bottom" id="next_div">
			<a role="button" class="btn btn-lg btn-primary btn-block"  @click="updateDebtInfo()"></a>
		</div>
	</section>
	<!-- //Content -->
</div>
</template>

<script>
import router from "@/comm/router.js";
import Common from "./../../assets/js/common.js";

export default {
  name: "DebtDetail",
  data() {
    return {
      debtVO: ""
    };
  },
  component: {},
  // computed () {
  // },
  beforeCreate() {},
  created() {
    this.getDebtInfoForUpdate();
    Common.enableBottom(true);
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    getDebtInfoForUpdate() {
      var thisObj = this;
      this.$http
        .get("/m/debt/getDebtInfoForUpdate.json", {
          params: { no_manage_info: thisObj.$store.state.no_manage_info }
        })
        .then(function(response) {
          var vo = response.data.debtVO;
          vo.style =
            "background-image:url('/m/fincorp/getFinCorpIcon.crz?cd_fc=" +
            vo.cd_fc +
            "')";
          thisObj.debtVO = vo;
          if ((vo.rep_method || "") === "") {
            vo.rep_method = "1";
          }
          vo.no_manage_info = thisObj.$store.state.no_manage_info;
        });
    },
    updateDebtInfo() {
      document.getElementById("interest").value = this.debtVO.interest;
      var thisObj = this;
      var form = document.getElementById("frmUpdateDebtInfo");
      var formData = new FormData(form);

      var vals = formData.keys();

      for (var a of vals) {
        console.log(a + "////////" + formData.get(a));
        if (formData.get(a) == "") {
          if (a == "interest") a = "int_interest";
          alert(
            $("#" + a)
              .prev()
              .text()
              .replace(" ", "") + " "
          );
          $("#" + a).focus();
          return;
        }
      }

      this.$http
        .post("/m/debt/updateDebtInfo.json", formData)
        .then(function(response) {
          if (response.data.code == "00") {
            thisObj.$toast.center(" .");
          } else {
            thisObj.$toast.center(" .");
          }
        });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
select {
  left: 120px;
  position: relative;
}
</style>



