<template>
  <section v-if="seen">
    <div class="credit-info">
      <p class="title"></p>
      <div class="wrap">
        <ul>
          <li>
            <p></p>
            <p>{{formatDateDot(shareInfo.dt_frt)}}</p>
          </li>
          <li>
            <p></p>
            <p>{{formatDateDot(shareInfo.dt_stt_offer)}}</p>
          </li>
        </ul>
      </div>
    </div>
    <div class="credit-info">
      <p class="title">    </p>
      <div class="wrap">
        <ul>
          <li>
            <p></p>
            <p>{{c_grade_credit!="" ? c_grade_credit : "-"}}</p>
          </li>
          <li>
            <p></p>
            <p>{{c_rating_credit!="" ? c_rating_credit : "-"}}</p>
          </li>
          <li>
            <p></p>
            <p>{{overdueCnt}}</p>
          </li>
        </ul>
        <template v-for="overdueInfo in overdueList">
          <p :key="overdueInfo.index">{{overdueInfo.change_contents}}</p>
          <p :key="overdueInfo.index" class="right"> : {{formatDateDot(overdueInfo.dt_info)}},  : {{overdueInfo.collector}}</p>
        </template>
      </div>
    </div>

    <div class="credit-info pb90">
      <p class="title"></p>
      <div class="wrap">
        <template v-for="debtInfo in debtList">
          <p :key="debtInfo.index" class="key">{{debtInfo.nm_fc}}</p>
          <ul :key="debtInfo.index" class="left">
            <li>
              <p></p>
              <p> : {{debtInfo.debt_type}}<br>
                 : {{formatDateDot(debtInfo.ymd_loan)}}<br>
                 : {{formatDateDot(debtInfo.ymd_loanend)}}<br>
                <template v-if="debtInfo.cd_type_deal != '3' && debtInfo.cd_type_deal != '6'"> : </template>
                <template v-else> : </template>
                {{debtInfo.amt_contract}} 
              </p>
            </li>
            <li>
              <p></p>
              <p> : {{debtInfo.amt_remain}} <br>
                 : {{debtInfo.ever_interest}} %<br>
                 :
                <template v-if="debtInfo.cd_type_deal != '3' && debtInfo.cd_type_deal != '6'">{{debtInfo.amt_repay}} </template>
                <template v-else>-</template>
              </p>
            </li>
          </ul>
        </template>
      </div>
    </div>

    <div class="btn-wrap" style="margin:0px 0px 100px 0px;">
      <a @click="reqUpdate();" class="stroke blue"> </a>
    </div>

    <div class="btn-wrap float">
      <a @click="settingExit();" class="solid blue box"> </a>
    </div>

  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";

import ko from "vee-validate/dist/locale/ko.js";

export default {
  name: "ShareDetail",
  data() {
    return {
      errMsg: "",
      seen: false,
      cd_share: "01", // - (01)
      seq_share: "", //seq
      share_status: "", //
      reqUpdateYn: "N", //
      shareInfo: "", //
      creditInfo: "", //
      c_grade_credit: "", //_
      c_rating_credit: "", //_
      overdueCnt: "", //
      overdueList: [], //list
      debtList: [] //list
    };
  },
  components: {},
  computed: {},
  beforeCreate() {},
  created() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = " ";

    this.seq_share = this.$route.query.seq_share;
    this.getDetailInfo();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //
    getDetailInfo: function() {
      var _this = this;
      this.$http
        .get("/m/customercenter/getShareInfoDetail.json", {
          params: { seq_share: _this.seq_share }
        })
        .then(response => {
          this.$store.state.title =
            response.data.shareInfo.offer_nm_person + this.$store.state.title; //title  setting
          _this.shareInfo = response.data.shareInfo;
          _this.creditInfo = response.data.creditInfo;
          _this.c_grade_credit = response.data.creditInfo.grade_credit;
          _this.c_rating_credit = response.data.creditInfo.rating_credit;
          _this.overdueCnt = response.data.overdueCnt;
          _this.overdueList = response.data.overdueList;
          _this.debtList = response.data.debtList;

          _this.seen = true;
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });
    },
    formatDateDot: function(data) {
      return Common.formatDateDot(data);
    },
    // 
    reqUpdate: function() {
      var _this = this;
      if ("Y" == _this.reqUpdateYn) {
        _this.$toast.center("   .");
        return false;
      }

      console.log("seq_share" + _this.seq_share);

      var formData = new FormData();
      formData.append("share_status", "06");
      formData.append("seq_share", _this.seq_share);
      this.$http
        .post("/m/customercenter/sendPersonShareInfoPush.json", formData)
        .then(function(response) {
          if ("00" == response.data.cdResult) {
            _this.$toast.center(" .");
            _this.reqUpdateYn = "Y";
          } else if ("01" == response.data.cdResult) {
            _this.$toast.center("  <br/>  .");
            _this.reqUpdateYn = "Y";
          } else {
            _this.$toast.center("  .");
            return false;
          }
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });
    },
    //
    settingExit: function() {
      var _this = this;

      console.log("seq_share" + _this.seq_share);

      var formData = new FormData();
      formData.append("seq_share", _this.seq_share);
      formData.append("share_status", "04");

      this.$http
        .post("/m/customercenter/updatePersonShareInfoSetStatus.json", formData)
        .then(function(response) {
          _this.$toast.center(response.data.message);
          if ("00" == response.data.cdResult) {
            _this.share_status = "04_1";
            _this.sendPush(); //
          }
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });
    },
    //push
    sendPush: function() {
      var _this = this;

      console.log("share_status" + _this.share_status);
      console.log("seq_share" + _this.seq_share);

      var formData = new FormData();
      formData.append("share_status", _this.share_status);
      formData.append("seq_share", _this.seq_share);
      this.$http
        .post("/m/customercenter/sendPersonShareInfoPush.json", formData)
        .then(function(response) {
          if ("00" == response.data.cdResult) {
            _this.$toast.center(" .");
            _this.goShareInfoMain();
          } else {
            _this.$toast.center(" .");
            return false;
          }
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });
    },
    //  
    goShareInfoMain: function() {
      var _this = this;
      this.$router.push({
        name: "shareMain",
        query: { cd_share: _this.cd_share, type_list: "req" }
      });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



