<template>
  <div id="wrapper" class="bg_white">
    <!-- Content -->
    <section id="content">
      <div class="container">
        <div class="check-all">
          <div class="checkbox" v-bind:class="checked"><label><input type="checkbox" id="checkall" v-model="chkAll" v-on:click="allChecked()">   </label></div>
        </div>
      </div>
      <!-- panel -->
      <div class="panel-group agree-panel" id="termsList">
        <div class="panel panel-default">
          <div class="panel-heading">
            <div class="checkbox">
              <label>
                <input type="checkbox" name="checkbox1" id="checkbox1" v-model="chkBox1" />
                <a href="" class="collapsed" role="button" data-toggle="collapse"><em>[]</em>  </a>
              </label>
            </div>
          </div>
          <div class="panel-collapse" id="panel1">
            <ul class="list-group">
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp1');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp2');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp3');">
                  <label>KCB  </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp4');">
                  <label>    </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp5');">
                  <label> 3  </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="panel panel-default">
          <div class="panel-heading">
            <div class="checkbox">
              <label>
                <input type="checkbox" name="checkbox2" id="checkbox2" v-model="chkBox2" />
                <a href="" class="collapsed" role="button" data-toggle="collapse"><em>[]</em>   </a>
              </label>
            </div>
          </div>
          <div class="panel-collapse" id="panel2">
            <ul class="list-group">
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp6');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp7');">
                  <label>   / </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp8');">
                  <label> </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp9');">
                  <label>   </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="panel panel-default">
          <div class="panel-heading">
            <div class="checkbox">
              <label>
                <input type="checkbox" name="checkbox3" id="checkbox3" v-model="chkBox3" />
                <a href="" class="collapsed" role="button" data-toggle="collapse"><em>[]</em>    </a>
              </label>
            </div>
          </div>
          <div class="panel-collapse" id="panel3">
            <ul class="list-group">
              <li>
                <a class="list-group-item" onclick="popAcceptTerms('hp10');">
                  <label>   </label>
                  <button type="button" class="btn-terms"></button>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <!-- //panel -->
      <div class="btn-fixed-bottom" id="next_div">
        <a role="button" id="confirmButton" class="btn btn-lg btn-primary btn-block" v-on:click="confirmedTerms()"></a>
      </div>
    </section>
    <!-- //Content -->
  </div>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";

export default {
  name: "certStep1",
  data() {
    return {
      uuid: "",
      errMsg: "",
      checked: "",
      chkAll: false,
      chkBox1: false,
      chkBox2: false,
      chkBox3: false,
      financeTerms: ""
    };
  },
  component: {},
  // computed () {
  // },
  beforeCreate() {},
  created() {
    window.resultCertSignInfo = this.resultCertSignInfo;

    if (Constant.userAgent == "Android") {
      window.Android.setEndApp("Y");
    }
    this.$store.state.title = " (7/7)";
  },
  beforeMount() {},
  mounted() {
    var _this = this;
    this.getTermsContent();
    $(":checkbox").change(function() {
      Common.affixBottom("hide");
      //  
      if (_this.chkBox1 && _this.chkBox2 && _this.chkBox3) {
        _this.chkAll = true;
        _this.checked = "check";
      } else {
        _this.chkAll = false;
        _this.checked = "";
      }

      if (_this.chkBox1 && _this.chkBox2) {
        Common.affixBottom("show");
      }

      //   parameter 
      if (_this.chkBox3) {
        _this.$store.state.user.isEventPush = true;
      }
    });
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //   
    checkExistCert: function() {
      if (Constant.userAgent == "iOS") {
        //     
        Jockey.on("resultCheckCert", function(param) {
          var iscert = "false";
          if (param.isCert == 1) iscert = "true";
          resultCheckCert(iscert);
        });
        Jockey.send("checkExistCert");
      } else if (Constant.userAgent == "Android") {
        window.Android.checkExistCert();
      }
    },
    /***
     * Native Call function
     ***/
    resultCheckFingerPrint: function(result) {
      console.log(result);
      if (result == true || result == 1) {
        this.chkFingerPrint = "Y";
      } else {
        this.chkFingerPrint = "N";
      }
    },
    //   ( )
    resultCheckCert: function(isCert) {
      if (isCert == "true") {
        //   
        if (Constant.userAgent == "iOS") {
          Jockey.on("checkPasswordCert", function(param) {
            resultCheckPasswordCert();
          });
          Jockey.send("checkPasswordCert", {
            noPerson: this.$store.state.user.noPerson,
            nmPerson: this.$store.state.user.nmPerson
          });
          //do nothing
        } else if (Constant.userAgent == "Android") {
          window.Android.checkPasswordCert(
            this.$store.state.user.noPerson,
            this.$store.state.user.nmPerson
          );
        }
      } else {
        //   
        this.$toast.center(" .");
        this.login();
      }
    },
    resultCheckPasswordCert: function(dn, cn) {
      //    
      this.$router.push({ name: "scrapSelFcLink", params: { dn: dn, cn: cn } });
    },
    allChecked: function() {
      var _this = this;
      if (_this.chkAll) {
        _this.chkAll = false;
      } else {
        _this.chkAll = true;
      }

      if (_this.chkAll) {
        _this.checked = "check";
        _this.chkBox1 = true;
        _this.chkBox2 = true;
        _this.chkBox3 = true;
      } else {
        _this.checked = "";
        _this.chkBox1 = false;
        _this.chkBox2 = false;
        _this.chkBox3 = false;
      }
    },
    confirmedTerms: function() {
      this.checkExistCert();
      //if (_this.chkBox1 && _this.chkBox2) {
      // this.$router.push({
      //   name: "scrapLoading",
      //   params: {
      //     dn: this.$route.params.dn,
      //     cn: this.$route.params.cn,
      //     normalMessage: "  <br>  .",
      //     smallMessage: " ."
      //   }
      // });
      //}
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



